import React, {Component} from 'react';

import './SVGGrid.scss';

import {COLORS} from '../../../../variables';
import Button from "../../../Controls/Button/Button";
import AppContext from "../../../../App-context";

import {isInViewport} from "../../../../variables";

let Snap;
let layout = {
	gridSize: 20,
	elements: []
};

try {
	Snap = require('snapsvg-cjs');
} catch (e) {
	console.log(e)
}

class SVGGrid extends Component {

	constructor() {
		super();
		this.setPOC = this.setPOC.bind(this);
		this.state = {
			gridSize: 20,
			buttons: {
				clear: {
					callback: () => {
						Snap(this.svgDiv).clear();
						layout.elements.length = 0;
						this.svgRender();
					},
					label: "Clear",
					style: {
						border: true,
						colorPrimary: COLORS.$grayDark,
						colorSecondary: COLORS.$grayLight
					}
				},
				content: {
					callback: () => {
						this.setElement("content");
						this.drawElement(this.svgDiv, this.state.buttons.content);
					},
					label: "Content",
					type: "grid-content",
					style: {
						border: true,
						colorPrimary: COLORS.$colorSecondary_1_4,
						colorSecondary: COLORS.$colorSecondary_1_2
					}
				},
				export: {
					callback: () => {
						console.log("exporting");
						this.createStylesheet();
					},
					label: "Open Layout in new tab",
					style: {
						border: false,
						colorPrimary: COLORS.$colorSecondary_2_3,
						colorSecondary: COLORS.$colorSecondary_2_1
					}
				},
				header: {
					callback: () => {
						this.setElement("header");
						this.drawElement(this.svgDiv, this.state.buttons.header);
					},
					label: "Header",
					type: "grid-header",
					style: {
						border: true,
						colorPrimary: COLORS.$colorSecondary_2_4,
						colorSecondary: COLORS.$colorSecondary_2_2
					}
				},
				sidebar: {
					callback: () => {
						this.setElement("sidebar");
						this.drawElement(this.svgDiv, this.state.buttons.sidebar);
					},
					label: "Sidebar",
					type: "grid-sidebar",
					style: {
						border: true,
						colorPrimary: COLORS.$colorPrimary_4,
						colorSecondary: COLORS.$colorPrimary_2
					}
				}
			}
		};
	}

	setGridSize(gridSize) {
		this.setState(state => ({gridSize}));
	};

	setElement(element) {
		this.setState(state => ({element}));
	};

	createStylesheet() {
		let a = `grid-template-columns: repeat(${layout.gridSize}, 1fr)`;
		let b = `grid-template-rows: repeat(${layout.gridSize}, 1fr)`;

		layout.elements.map(element => console.log(element.position));
	}

	setPOC() {
		let poc = +this.svgDiv.dataset.poc;
		if (isInViewport(this.svgDiv)) {
			this.context.setCurrentPOC(poc)
		} else {
			this.context.setCurrentPOC(0)
		}
	}

	svgRender() {
		//let snap = Snap(this.svgDiv);
		let {height, width} = window.getComputedStyle(this.svgDiv, null),
			snap = Snap(this.svgDiv),
			pattern,
			patternDim,
			path;

		//ToDo update instead of deleting
		snap.clear();

		height = height.replace("px", "");
		width = width.replace("px", "");

		patternDim = {
			height: height / this.state.gridSize,
			width: width / this.state.gridSize
		};

		path = `M0,0H${patternDim.width}V${patternDim.height}H0V0`;
		pattern = snap.path(path).attr({
			fill: "none",
			stroke: COLORS.$grayMedium,
			strokeWidth: .5
		}).pattern(0, 0, patternDim.width, patternDim.height);

		snap.attr({
			viewBox: "0,0," + width + "," + height,
			fill: pattern
		});

		snap.rect(0, 0, width, height).attr({
			fill: pattern
		});
	}

	drawElement(svgNode, element) {
		let {colorPrimary, colorSecondary} = element.style,
			x_start,
			x_end,
			y_start,
			y_end,
			gridSize = this.state.gridSize,
			svgDiv = this.svgDiv,
			{height, width} = window.getComputedStyle(svgDiv, null),
			snap = Snap(svgNode),
			rect_temp;

		height = height.replace("px", "");
		width = width.replace("px", "");
		height = height / gridSize;
		width = width / gridSize;

		function draw_Rect(x_start, x_end, y_start, y_end) {

			x_start = Snap.snapTo(width, x_start, width / 2 + 1);
			x_end = Snap.snapTo(width, x_end, width / 2 + 1);
			y_start = Snap.snapTo(height, y_start, height / 2 + 1);
			y_end = Snap.snapTo(height, y_end, height / 2 + 1);

			[x_start, x_end] = x_start >= x_end ? [x_end, x_start] : [x_start, x_end];
			[y_start, y_end] = y_start >= y_end ? [y_end, y_start] : [y_start, y_end];
			x_end = Math.abs(x_end - x_start);
			y_end = Math.abs(y_end - y_start);

			return snap.rect(
				x_start,
				y_start,
				x_end,
				y_end
			);
		}

		function draw_Start(e) {
			svgNode.addEventListener("mousemove", draw_Move);
			x_start = e.offsetX;
			y_start = e.offsetY;
		}

		function draw_Move(e) {
			x_end = e.offsetX;
			y_end = e.offsetY;

			if (rect_temp) {
				rect_temp.remove();
			}
			rect_temp = draw_Rect(x_start, x_end, y_start, y_end);
			rect_temp.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				"stroke-dasharray": "10,10",
				strokeWidth: 1,
				opacity: .5
			});
		}

		function draw_Stop() {
			let rect, text;


			/*x_start = Snap.snapTo(width, x_start, width / 2 + 1);
			x_end = Snap.snapTo(width, x_end, width / 2 + 1);
			y_start = Snap.snapTo(height, y_start, height / 2 + 1);
			y_end = Snap.snapTo(height, y_end, height / 2 + 1);*/


			if (rect_temp) {
				rect_temp.remove();
			}
			svgNode.removeEventListener("mousemove", draw_Move);

			rect = draw_Rect(x_start, x_end, y_start, y_end);

			let {x, y, width: rectWidth, height: rectHeight} = rect.attr(),
				grid_col_Start = Math.round(+x / +width) + 1,
				grid_col_End = grid_col_Start + Math.round(+rectWidth / +width) -1,

				grid_row_Start = Math.round(+y / +height) + 1,
				grid_row_End = grid_row_Start + Math.round(+rectHeight / +height) -1;


			layout.elements.push({
				id: rect.id,
				type: element.type,
				position: {
					"grid-row-start": grid_row_Start,
					"grid-row-end": grid_row_End,
					"grid-column-start": grid_col_Start,
					"grid-column-end": grid_col_End
				},
				resolution: 1
			});

			rect.attr({
				fill: colorPrimary,
				stroke: colorSecondary,
				strokeWidth: 1,
				class: element.type
			});

			/*
			text = snap.text(rect.getBBox().x + height / 2, rect.getBBox().y + width / 2, element.label);

			text.attr({
					fill: colorSecondary,
					fontFamily: "sans-serif",
					fontSize: `${gridSize * 2}px`,
					stroke: colorSecondary
				});
				*/

			svgNode.removeEventListener("mousedown", draw_Start);
			svgNode.removeEventListener("mouseup", draw_Stop);
			svgNode.style.cursor = "default";
		}

		svgNode.addEventListener("mousedown", draw_Start);	//svgNode = area
		svgNode.addEventListener("mouseup", draw_Stop);
		svgNode.style.cursor = "crosshair";
	};

	componentDidMount() {
		this.svgRender();
		window.addEventListener("scroll", this.setPOC);
	}

	componentDidUpdate() {
		//this.svgRender();
	}

	componentWillUnmount() {
		//ToDo delete svg
		window.removeEventListener("scroll", this.setPOC);
	}

	render() {
		return (
			<AppContext.Consumer>
				{({appState, setCurrentPOC}) => {
					return (
						<React.Fragment>
							<h1>Konzept für das dynamische Erstellen von Seitenlayouts</h1>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>

							<Button {...this.state.buttons.header}/>
							<Button {...this.state.buttons.sidebar}/>
							<Button {...this.state.buttons.content}/>
							<Button {...this.state.buttons.clear}/>
							<Button {...this.state.buttons.export}/>
							<svg data-poc={1} className="gridWrapper" ref={d => this.svgDiv = d}/>


							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p><p>Lorem Ipsum</p>
							<p>Lorem Ipsum</p>
						</React.Fragment>
					)
				}}
			</AppContext.Consumer>
		)
	}
}

SVGGrid.contextType = AppContext;

export default SVGGrid;
